@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,600);

@media screen and (orientation:landscape) {
body{
	font-family: 'Work Sans', sans-serif;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#243949), to(#517fa4));
  background: -webkit-linear-gradient(top, #243949 0%, #517fa4 100%);
  background: -o-linear-gradient(top, #243949 0%, #517fa4 100%);
  background: linear-gradient(to bottom, #243949 0%, #517fa4 100%);
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
}

@media screen and (orientation:portrait) {
body{
	font-family: 'Work Sans', sans-serif;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#243949), to(#517fa4));
  background: -webkit-linear-gradient(top, #243949 0%, #517fa4 100%);
  background: -o-linear-gradient(top, #243949 0%, #517fa4 100%);
  background: linear-gradient(to bottom, #243949 0%, #517fa4 100%);
  background-repeat: no-repeat;
  height: 100vh;
  width:96%;
  overflow-y: hidden;
}
}



